<template>
	<div>
		<div class="secction diyscrollbar">
			<list-top placeholder="输入文章名/标签字查找" btnText="添加文章" v-on:addBtn="addArticle" v-on:search="searchArticleAction"></list-top>
			<articles-list v-bind:totalArticles="totalArticles" v-bind:currentPage="currentPage" v-on:getArticles="getArticles" v-on:amend="amendArticle" v-on:read="readArticle" v-bind:articles="articlesList"></articles-list>
		</div>
		<el-dialog v-bind:title="dialog.title" v-model="dialog.control" top="10px" size="full">
			<article-write v-on:getArticles="getArticles" v-on:close="closeDialog" v-bind:article="dialog.article"></article-write>
		</el-dialog>
	</div>
</template>

<script>
	import articleWrite from './article/ArticleWrite.vue'
	import listTop from './public/ListTop.vue'
	import articlesList from './article/ArticlesList.vue'
	export default {
		name: 'articlesList',
		components: {
			'list-top': listTop,
			'articles-list': articlesList,
			'article-write': articleWrite,
		},
		data() {
			return {
				dialog: {
					article: {},
					title: '',
					control: false, //控制dialog的显示与关闭
				},
				currentPage: 1,
				totalArticles: 200,
				articlesList: [],
				currentNum: 0, //每页多少条
			}
		},
		created() {
			this.getArticles();
		},
		methods: {
			getArticles(currentNum) { //获取数据
				this.currentNum = currentNum;
				/*this.$http.get("../../../../static/url/articles.json").then((response) => {
					console.log(response.data)
					this.articlesList = response.data;
				}, (response) => {
					this.$message.error('获取文章失败');
				});*/
				console.log("获取全部文章mmmm");
				this.$http.get("admin/article").then((response) => {
					console.log(response.data);
					//this.articlesList.push.apply(this.tagsList,response.data);
					this.articlesList=response.data;
				}, (response) => {
					this.$message.error('获取文章失败');
				});
			},
			readArticle() {

			},
			addArticle() { //显示添加的dialog
				this.dialog.article = {
					title: '',
					tagSet: [],
					state: 1,
					content: '',
				};
				this.openDialog();
				this.dialog.title = "添加文章";
				if(!testEditor.settings!="undefined") {
					createdEditormd('editormd', ' '); //初始化Editor.md
				} else {
					testEditor.settings.markdown = ' ';
				}

			},
			amendArticle(article) { //显示修改的dialog
				this.dialog.article = article;
				this.openDialog();
				this.dialog.title = "修改文章";
				console.log(article.content);
				if(testEditor.settings!="undefined") {
					createdEditormd('editormd'); //初始化Editor.md
				} else {
					testEditor.settings.markdown = article.content;
				}

			},
			searchArticleAction(searchKey) { //搜索按钮

			},
			closeDialog() {
				this.dialog.control = false;
			},
			openDialog() {
				this.dialog.control = true;
			},
		},
	}
</script>

